export default {
	data() {
		return {
			GoodsObj: {},
			GoodsInfo: {},
			GoodsInfonew: {},
			    // 商品是否被收藏
			    isCollect:false
		};
	},

	components: {},
	props: {},

	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		var ids = options.id;
		uni.showToast({
			title: "编号" + ids,
			icon: "none"
		});
		//获得当前对象
		var that = this
		uni.request({
			url: 'http://localhost:8080/commodity/getById?id=' + ids,
			method: 'POST',
			success: res => {
				this.GoodsInfo = res.data;
				console.log(res.data)
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		//点击图片预览
		imgYu: function(event) {
			var src = event.currentTarget.dataset.src; //获取data-src

			var imgList = event.currentTarget.dataset.list; //获取data-list
			//图片预览


		},

		setData: function(obj, callback) {
			let that = this;
			let keys = [];
			let val, data;
			Object.keys(obj).forEach(function(key) {
				keys = key.split('.');
				val = obj[key];
				data = that.$data;
				keys.forEach(function(key2, index) {
					if (index + 1 == keys.length) {
						that.$set(data, key2, val);
					} else {
						if (!data[key2]) {
							that.$set(data, key2, {});
						}
					}

					data = data[key2];
				});
			});
			callback && callback();
		},
		//点击加入购物车
		handleCartAdd() {
			console.log(this.GoodsInfo);
			// 1 获取缓存中的购物车 数组
			let cart = uni.getStorageSync("cart") || [];
			// 2 判断 商品对象是否存在于购物车数组中
			let index = cart.findIndex(v => v.id === this.GoodsInfo.id);
			if (index === -1) {
				//3  不存在 第一次添加
				this.GoodsInfonew.goodsName = this.GoodsInfo.goodsName;
				this.GoodsInfonew.price = this.GoodsInfo.price;
				this.GoodsInfonew.picturepath = this.GoodsInfo.picturepath;
				this.GoodsInfonew.number = 1;
				this.GoodsInfonew.id = this.GoodsInfo.id;
				cart.push(this.GoodsInfonew);
			} else {
				// 4 已经存在购物车数据 执行 num++
				cart[index].number++;
			}
			// 5 把购物车重新添加回缓存中
			uni.setStorageSync("cart", cart);
			// 6 弹窗提示
			uni.showToast({
				title: '加入成功',
				icon: 'success',
				// true 防止用户 手抖 疯狂点击按钮 
				mask: true
			});
		},
		// 点击 商品收藏图标
		  handleCollect(){
			  console.log(this.GoodsInfo);
		    let isCollect=false;
		    // 1 获取缓存中的商品收藏数组
		    let collect=uni.getStorageSync("collect")||[];
		    // 2 判断该商品是否被收藏过
		    let index=collect.findIndex(v=>v.id===this.GoodsInfo.id);
		    // 3 当index！=-1表示 已经收藏过 
		    if(index!==-1){
		      // 能找到 已经收藏过了  在数组中删除该商品
		      collect.splice(index,1);
		      isCollect=false;
		      uni.showToast({
		        title: '取消成功',
		        icon: 'success',
		        mask: true
		      });
		        
		    }else{
		      // 没有收藏过
		      collect.push(this.GoodsInfo);
		      isCollect=true;
		      uni.showToast({
		        title: '收藏成功',
		        icon: 'success',
		        mask: true
		      });
		    }
		    // 4 把数组存入到缓存中
		    uni.setStorageSync("collect", collect);
		    // 5 修改data中的属性  isCollect
		    this.setData({
		      isCollect
		    })
		      
		      
		  },
	}
};
